import React from 'react';
import './index.css';

export default class SizeTable extends React.Component {
    render() {
        return (
            <div id="size-table" className="size-table">
                <div className='table'>
                    <div className="table-headline">
                        <h2 className="table-caption">Таблица размеров</h2>
                        <a href="#" className="table-close">✖</a>
                    </div>
                    <table>
                        <thead>
                            <tr>
                                <th>Окружность головы, см</th>
                                <th>Диаметр дна, см</th>
                                <th>Глубина шапочки, см</th>
                                <th>Примерный возраст</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>55</td>
                                <td>17,5</td>
                                <td rowSpan='3'>20-21</td>
                                <td rowSpan='3'>Ребенок старше 5 лет, женщина</td>
                            </tr>
                            <tr>
                                <td>56</td>
                                <td>17,8</td>
                            </tr>
                            <tr>
                                <td>57</td>
                                <td>18,2</td>
                            </tr>
                            <tr>
                                <td>58</td>
                                <td>18,5</td>
                                <td rowSpan='3'>21-23</td>
                                <td rowSpan='3'>Взрослый мужчина</td>
                            </tr>
                            <tr>
                                <td>59</td>
                                <td>18,8</td>
                            </tr>
                            <tr>
                                <td>60</td>
                                <td>19,1</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>

        );
    }
}